import React, { Component } from "react";
import Header from "../components/Header";
import {
  Grid,
  TextField,
  Select,
  MenuItem,
  InputLabel,
  OutlinedInput,
  InputAdornment,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  Typography,
  DialogActions,
  Button,
} from "@material-ui/core";
import HelpOutlineIcon from "@material-ui/icons/HelpOutline";

export default class AddCampaign extends Component {
  state = {
    showDiscountModal: false,
    showCodeModal: false,
    showNumberModal: false,
  };
  render() {
    return (
      <div>
        <Header showSave hideSearch />
        <Grid
          container
          direction="row"
          justify="flex-start"
          alignItems="flex-start"
        >
          <div className="marginRight">
            <h4>Information</h4>
            <div>
              <TextField
                placeholder="Name"
                id="outlined-basic"
                variant="outlined"
              />
            </div>
            <div>
              <TextField
                fullWidth
                variant="outlined"
                className="marginTop"
                type="date"
                defaultValue="2017-05-24"
              />
            </div>
            <div>
              <TextField
                fullWidth
                variant="outlined"
                className="marginTop"
                type="date"
                defaultValue="2017-05-24"
              />
            </div>
            <div>
              <TextField
                className="marginTop"
                placeholder="Country"
                variant="outlined"
              />
            </div>
            <div>
              <TextField
                className="marginTop"
                placeholder="Currency"
                variant="outlined"
              />
            </div>
          </div>
          <div className="marginRight">
            <h4>Action</h4>
            <div>
              <Select variant="outlined" fullWidth value="20">
                <MenuItem value="10">Ten</MenuItem>
                <MenuItem value="20">Twenty</MenuItem>
              </Select>
            </div>
            <div>
              <OutlinedInput
                className="marginTop"
                placeholder="Order Discount %"
                endAdornment={
                  <InputAdornment
                    onClick={() => this.setState({ showDiscountModal: true })}
                    position="end"
                  >
                    <IconButton edge="end">
                      <HelpOutlineIcon />
                    </IconButton>
                  </InputAdornment>
                }
              />
            </div>
          </div>
          <div>
            <h4>Condition</h4>
            <div>
              <Select variant="outlined" fullWidth value="20">
                <MenuItem value="10">Ten</MenuItem>
                <MenuItem value="20">Twenty</MenuItem>
              </Select>
            </div>
            <div>
              <OutlinedInput
                className="marginTop"
                placeholder="Code"
                endAdornment={
                  <InputAdornment
                    onClick={() => this.setState({ showCodeModal: true })}
                    position="end"
                  >
                    <IconButton edge="end">
                      <HelpOutlineIcon />
                    </IconButton>
                  </InputAdornment>
                }
              />
            </div>
            <div>
              <OutlinedInput
                className="marginTop"
                placeholder="Number of usages"
                endAdornment={
                  <InputAdornment
                    onClick={() => this.setState({ showNumberModal: true })}
                    position="end"
                  >
                    <IconButton edge="end">
                      <HelpOutlineIcon />
                    </IconButton>
                  </InputAdornment>
                }
              />
            </div>
          </div>
        </Grid>

        {/* //Discount Modal */}
        <Dialog open={this.state.showDiscountModal}>
          <DialogTitle>Order Discount %</DialogTitle>
          <DialogContent dividers>
            <Typography gutterBottom>
              This discount will be apply to the order total excluding VAT and
              Shipment.
            </Typography>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={() => this.setState({ showDiscountModal: false })}
              variant="contained"
              color="primary"
            >
              Ok
            </Button>
          </DialogActions>
        </Dialog>

        {/* //Code Modal */}
        <Dialog open={this.state.showCodeModal}>
          <DialogTitle>Campaign Code</DialogTitle>
          <DialogContent dividers>
            <Typography gutterBottom>
              The user needs to fill in the campaigncode to get the discount.
              The code is CASE SENSITIVE.
            </Typography>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={() => this.setState({ showCodeModal: false })}
              variant="contained"
              color="primary"
            >
              Ok
            </Button>
          </DialogActions>
        </Dialog>

        {/* //Number of useages Modal */}
        <Dialog open={this.state.showNumberModal}>
          <DialogTitle>Number of Usages</DialogTitle>
          <DialogContent dividers>
            <Typography gutterBottom>
              How many times this campaign code can be used before it is
              consumed.
            </Typography>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={() => this.setState({ showNumberModal: false })}
              variant="contained"
              color="primary"
            >
              Ok
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}
