import React, { Component } from "react";
import Header from "../components/Header";

import {
  Table,
  TableContainer,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
  Paper,
  Switch,
} from "@material-ui/core";

import ReactDragListView from "react-drag-listview";
import ImportExport from "@material-ui/icons/ImportExport";
import { connect } from "react-redux";

import { updateCampaigns } from "../redux/actions/ActionCreator";

class Campaigns extends Component {
  constructor(props) {
    super(props);
    const data = [];
    for (let i = 1; i < 6; i++) {
      data.push({
        title: `rows${i}`,
      });
    }
    this.state = {
      data,
      reorder: false,
      hideOptions: true,
      selectedIndex: -1,
    };
  }
  componentDidMount() {
    this.props.updateCampaigns(this.state.data);
  }
  render() {
    console.log(this.props);
    const that = this;
    const dragProps = {
      onDragEnd(fromIndex, toIndex) {
        const data = [...that.state.data];
        const item = data.splice(fromIndex, 1)[0];
        data.splice(toIndex, 0, item);
        that.setState({ data });
      },
      nodeSelector: "tr",
      handleSelector: "section",
    };

    return (
      <div>
        <Header
          reorder={(reorder) => this.setState({ reorder })}
          hideOptions={this.state.hideOptions}
        />
        <TableContainer className="marginTop" component={Paper}>
          <ReactDragListView {...dragProps}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Name</TableCell>
                  <TableCell>Country</TableCell>
                  <TableCell>Currency</TableCell>
                  <TableCell>StartDate</TableCell>
                  <TableCell>StartDate</TableCell>
                  <TableCell align="right">Active</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.state.data.map((item, index) => (
                  <TableRow
                    key={index}
                    onClick={() =>
                      this.setState({
                        hideOptions: false,
                        selectedIndex: index,
                      })
                    }
                    className={this.state.selectedIndex === index && "blueBg"}
                  >
                    <TableCell>{item.title}</TableCell>
                    <TableCell>SE</TableCell>
                    <TableCell>SEK</TableCell>
                    <TableCell>2020-04-04</TableCell>
                    <TableCell>2021-04-04</TableCell>
                    <TableCell align="right">
                      {this.state.reorder ? (
                        <section>
                          <ImportExport />
                        </section>
                      ) : (
                        <Switch
                          // checked={state.checkedB}
                          // onChange={handleChange}
                          // name="checkedB"
                          size="medium"
                          color="primary"
                        />
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </ReactDragListView>
        </TableContainer>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  campaigns: state.campaigns,
});
const mapDispatchToProps = (dispatch) => {
  return {
    updateCampaigns: (payload) => dispatch(updateCampaigns(payload)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Campaigns);
